:root {
  --background: #ffffff;
  --foreground: #171717;
}
@media (prefers-color-scheme: dark) {
  :root {
    --background: white;
    --foreground: white;
  }
}
.katibeh-regular {
  font-family: "Katibeh", serif;
  font-weight: 400;
  font-style: normal;
}
html {
  height: 100%;
  width: 100%;
}
body {
  color: var(--foreground);
  background-color: white;
  font-family: Arial, Helvetica, sans-serif;
  height: 100%;
  width: 100%;
  margin: 0;
  padding: .5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.main-container {
  flex: 1;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.landing {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 50%;
  height: 75%;
}
.logo {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #171717;
}
.logo h1 {
  font-size: 120px;
  font-family: 'Katibeh', serif;
}
.presentation {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 2.5rem;
  width: 100%;
}
.presentation p {
  font-size: 20px;
  color: #171717;
  text-align: center;
  font-family: "Poppins", sans-serif;
  font-weight: 200;
  font-style: italic;
}
.buttons-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  color: black;
  gap: 1rem;
}
.buttons-container button {
  width: 20%;
  background-color: #0B87DC;
  border-radius: 15px;
  padding: .75rem;
  color: white;
  font-size: 15px;
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  border: none;
  cursor: pointer;
}
.buttons-container button:hover {
  background-color: #f9f9f9;
  color: #0B87DC;
  transition-duration: 600ms;
}
.buttons-container button:active {
  background-color: #0B87DC;
  color: white;
  transition-duration: 300ms;
  box-shadow: inset 0 0 20px #f9f9f9;
}
